.modal{background-color:#000;background-color:rgba(0,0,0,.4);bottom:0;height:100%;left:0;overflow:hidden;position:fixed;right:0;top:0;width:100%;z-index:9999}.modal .section{margin-bottom:1rem}.modal-content{-webkit-animation-duration:.6s;-webkit-animation-name:animatetop;animation-duration:.6s;animation-name:animatetop;background-color:#fff;border:3px solid #ec6907;border-bottom-left-radius:8px;border-bottom-right-radius:8px;border-top:0;bottom:0;box-shadow:0 4px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);left:0;margin:auto;max-height:100vh;overflow:auto;padding:0;position:absolute;right:0;top:0}.modal-content .modal-header{background-color:#ec6907;color:#fff;font-weight:400;padding:.25rem;position:relative;text-transform:uppercase}.modal-content .modal-body{padding:1.5rem 1rem 1rem;white-space:normal}.modal-content .modal-footer{padding:0 1rem;text-align:right}@-webkit-keyframes animatetop{0%{opacity:0;top:-100px}to{opacity:1}}@keyframes animatetop{0%{opacity:0;top:-100px}to{opacity:1}}.close-modal{transition:.5s}.close-modal__button{cursor:pointer;display:inline-block;height:1.25rem;opacity:1;outline:none;overflow:hidden;position:absolute;right:.25rem;top:50%;transform:translateY(-50%);width:1.25rem}.close-modal__button:hover:after,.close-modal__button:hover:before{background:#c5c5c5}.close-modal__button:after,.close-modal__button:before{background:#fff;border-radius:2px;content:"";height:2px;position:absolute;top:50%;width:100%}.close-modal__button:before{transform:rotate(45deg)}.close-modal__button:after{transform:rotate(-45deg)}@media only screen and (min-width:480px){.modal-content{min-width:448px;overflow:visible;position:relative;top:50%;transform:translateY(-50%);width:40%}}