.header{background-color:#fff;height:7.25rem;left:50%;max-width:1440px;position:fixed;transform:translateX(-50%);width:100%;z-index:3}.header__title{font-size:3em;padding:1.5rem;position:absolute;top:50%;transform:translateY(-50%);width:calc(100% - 20rem)}.header .logo{text-align:right;text-decoration:none}.header .logo__svg svg{fill:#ec6907;height:3.375rem;width:9rem}.header .logo__caption{color:#ec6907;font-weight:700;padding-right:.25rem;text-transform:uppercase}.header .logo__wrapper{max-width:20rem;padding:1rem;position:absolute;right:0;text-align:right;top:0;z-index:5}.header--fluid{background-color:transparent;margin:0;max-width:100%}.header.--negative .logo__svg svg{fill:#fff}.header.--negative .logo__caption{color:#fff}